@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Ubuntu', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,
body,
#potato,
.container {

    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.movies {
    display: flex;
    justify-content: space-between;
    align-items: felx-start;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
}

.movies .movies__movie {
    width: 48%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies img {
    position: relative;
    top: -50px;
    max-width: 180px;
    width: 100%;
    margin-right: 20px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}


.movies .movie__title,
.movies .movie__year {
    font-weight: 700;
    font-style: italic;
}

.movies .movie__title {
    margin-bottom: 5px;
    font-size: 21px;
    color: #2c2c2c;
}

.movie__data .movie__year {
    opacity: 0.6;
    margin: 10px 0 0 0;
    padding: 0;
}


.movie__data .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px;
}

.movie__genres li {
    margin-right: 10px;
}